    export const todos = table('todos', [
        primaryKey(integer('id'), true),
        notNull(text('title')),
        text('text'),
        defaultValue(integer('completed'), 0), // 0 = false, 1 = true (SQLite doesn't have boolean)
        defaultValue(text('created_at'), new Date().toISOString())
    ]);

